/* Hero Section */

.home{
    width: 100%;
    height: calc(100vh - 65px);
    min-height: 500px;
    display: flex;
    align-items: center;
}
.info {
    height: 80%;
    flex: .5;
    /*border: 1px solid blue;*/
    display: flex;
    flex-direction: column;
    padding: 25px 50px;
    justify-content: center;
    /*background: rgba(68, 127, 180, .5);*/
    background: var(--gradient-blue-background);
}
.info h1{
    font-family: "Josefin Sans", sans-serif;
    margin: 10px 0;
    font-size: 40px;
    color: white;
    /*text-shadow: 2px 2px black;*/
}
.info h1 span{
    color: #3ce83c;
}
.info p{
    font-family: "Montserrat", cursive;
    margin: 15px 0;
    color: black;
    font-size: 14px;
}
.take-to-form-container button{
    padding: 10px 10px 10px 0;
    display: flex;
    justify-content: start;
    align-items: center;
    border: none;
    background: none;
    font-family: "Sofia Sans Extra Condensed", sans-serif;
    font-size: 20px;
}
.take-to-form-container .text{
    padding: 5px 0;
}
.take-to-form-container .arrow{
    transform: rotateY(180deg);
    font-size: 40px;
    margin: 0 10px;
    transition: all .4s;
}
.take-to-form-container button:hover .text{
    text-underline-color: dodgerblue;
    text-decoration: 1px black underline;
}
.take-to-form-container button:hover  .arrow{
    transform: translateX(10px) rotateY(0) scaleX(120%);
    color: white;
}

.vector{
    height: 80%;
    flex: .5;
}
.vector > div {
    background: url('../images/Counseling-PNG-Isolated-Picture.png') no-repeat center;
    background-size: contain;
    min-height: 250px;
    width: 100%;
    height: 100%;

}



@media screen and (max-width: 800px){
    .home{
        flex-direction: column-reverse;
        justify-content: start;
        height: auto;
    }
    .info{
    }
    .vector{
        flex: .5;
        width: 100%;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .vector div{
        width: 100%;
        height: 100%;
        background-size: contain;
    }
}



/* Responsive View */
@media screen and (max-width: 300px){
    .home{
        height: auto;
    }
}


/* About Section */
.about{
    display: flex;
    margin-top: 100px;
    flex-direction: column;
}
.heading{
    height: 100px;
    background: var(--gradient-blue-background);
    font-family: "Josefin Sans", sans-serif;
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.offers{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 25px;
    min-height: 400px;
    padding: 25px;
}
.offers > div{
    background: white;
    display: flex;
    flex-direction: column;
    padding: 25px 20px;
    align-items: center;
    border-radius: 2px;
    box-shadow: 2px 2px 2px #ababab;
    transition: box-shadow .1s linear;
    min-width: 250px;
}
.offers h3{
    font-size: 20px;
    text-align: center;
    font-family: "Oswald", sans-serif;
    margin-bottom: 25px;
}
.offers p{
    font-family: "Titillium Web", sans-serif;
}
.offers span{
    font-size: 75px;
    color: #1C82AD;
    margin-top: auto;
    width: fit-content;
}
.offers > div:hover{
    box-shadow: none;
}


/* Responsive View */
@media screen and (max-width: 1150px){
    .offers{
        overflow: scroll;
    }
}







